.unreadMark{
	position: absolute;
	right: 0;
	bottom: 30px;
}
.chat-content {
	height: calc(100vh - 200px);
	padding: 25px;
	box-sizing: border-box;
	overflow-y: scroll;
	width: 100%;
	position: relative;
	overflow: auto;
}

.user-sent{
	float: right;
	clear: both;
	padding: 20px;
	box-sizing: border-box;
	word-wrap: break-word;
	white-space: pre-line;
	margin-top: 10px;
	background-color: #707BC4;
	color: white;
	width: 300px;
	border-radius: 10px;
}

.friend-sent{
	float: left;
	clear: both;
	padding: 20px;
	box-sizing: border-box;
	word-wrap: break-word;
	white-space: pre-line;
	margin-top: 10px;
	background-color: #707BC4;
	color: white;
	width: 300px;
	border-radius: 10px;
}

.user-sent-message{
	-webkit-filter: drop-shadow(-1px -1px 2px rgba(0,0,0, .10)) drop-shadow(1px 2px 2px rgba(0,0,0, .15));
	        filter: drop-shadow(-1px -1px 2px rgba(0,0,0, .10)) drop-shadow(1px 2px 2px rgba(0,0,0, .15));
	margin: 1rem;
	margin-bottom: 40px;
	padding: 0.9rem 1.2rem;
	position: relative;
	font-family: 'Source Sans Pro', sans-serif;
	font-weight: 400;
	background: #8ca8d8;
	color: white;
	float: right;
	clear: both;
	word-wrap: break-word;
	white-space: pre-line;
	width: 300px;
	border-right: 5px solid #ee6e73;
	border-left: none;
	border-radius: 5px;
}

.user-sent-message::before{
	border: 12.5px solid transparent;
	border-top: 12.5px solid #8ca8d8;
	border-bottom: 0;
	height: 0;
	width: 0;
	border-top-width: 25px;
	content: '';
	display: block;
	position: absolute;
	left: 3rem;
	bottom: -25px;
	-webkit-transform-origin: center;
	        transform-origin: center;
	-webkit-transform: rotate(90deg) skew(-25deg) translateY(16.66666667px);
	        transform: rotate(90deg) skew(-25deg) translateY(16.66666667px);
}

.user-sent-message cite{
	position: absolute;
	bottom: -2rem;
	left: 4.5rem;
	font-size: 1rem;
	font-style: normal;
	font-weight: 300;
	letter-spacing: 0.5px;
	color: black;
}

.friend-sent-message {
	-webkit-filter: drop-shadow(-1px -1px 2px rgba(0,0,0, .10)) drop-shadow(1px 2px 2px rgba(0,0,0, .15));
	        filter: drop-shadow(-1px -1px 2px rgba(0,0,0, .10)) drop-shadow(1px 2px 2px rgba(0,0,0, .15));
	margin: 1rem;
	margin-bottom: 40px;
	padding: 0.9rem 1.2rem;
	position: relative;
	font-family: 'Source Sans Pro', sans-serif;
	font-weight: 400;
	background: #707BC4;
	color: white;
	float: left;
	clear: both;
	word-wrap: break-word;
	white-space: pre-line;
	width: 300px;
	border-radius: 5px;
}

.friend-sent-message::before{
	border: 12.5px solid transparent;
	border-top: 12.5px solid #707BC4;
	border-bottom: 0;
	height: 0;
	width: 0;
	border-top-width: 25px;
	content: '';
	display: block;
	position: absolute;
	right: 3rem;
	bottom: -25px;
	-webkit-transform-origin: center;
	        transform-origin: center;
	-webkit-transform: rotate(270deg) skew(25deg) translateY(16.66666667px);
	        transform: rotate(270deg) skew(25deg) translateY(16.66666667px);
}

.friend-sent-message cite{
	position: absolute;
	bottom: -2rem;
	font-size: 1rem;
	font-style: normal;
	font-weight: 300;
	letter-spacing: 0.5px;
	color: black;
}
.chat-send-btn{
	background-color: #707BC4;
	clear: both;
	position: absolute;
	bottom: 5px;
	right: 0.1px;
}

#chat-text{
	width: 90%;
}

#message {
	resize: none;
	background-color: rgb(245,245,245);
	border-color: rgb(230,230,230);
	height: 100px;
}

#message:focus {
	outline: 0;
}

.text-box {
	width: calc(100% - 20px);
	margin-right: 0;
}
@media only screen and (min-width: 1600px){
	.text-box{
		width: 1197.5px;
		margin: auto;
	}
}
.chatsTemplate {
	padding-left: 1.75rem;
	padding-right: 1.75rem;
	margin-left : 300px;
}
@media only screen and (max-width: 992px){
	.chatsTemplate{
		margin-left: 0;
	}
}

@media only screen and (min-width: 1600px){
	.chatMessages{
		width: 1247.5px;
		margin: auto;
	}
}


.createService .card.hidden {
  padding: 0;
  background-color: transparent;
}
.createService .input-field {
  margin-top: 5rem;
}
.createService .input-field label {
    /* padding: .2rem; */
    top: -30px;
    /* border-bottom: 1px solid black; */
    /* border-right: 1px solid black; */
    /* border-top: 1px solid black; */
    padding: .2rem .6rem;
    /* padding: .6rem; */
    background: aliceblue
}
.createService .material-icons {
  vertical-align: middle;
  font-size: 20px;
  padding-bottom: 2px;
}
.createService .material-icons.close-btn {
  cursor: pointer;
  position: absolute;
  left: 0;
  padding: .2rem;
  opacity: 1;
  background-color: rgba(0, 0, 0, .3);
}
.createService .material-icons.close-btn:hover {
  opacity: .8;
}
.createService .image-btn img.responsive-img {
  position: absolute;
  height: 250px;
}
.createService .input-field.none {
  display: none;
}
.createService .input-field textarea {
  height: 15rem !important;
  overflow-y: scroll !important;
}
.createService .btn-wrapper {
  display: flex;
  justify-content: center;
  margin: 3rem 0;
}

.createService .file-field.input-field .image-btn {
  position: relative;
  height: 250px;
  margin-bottom: 2rem;
}
.createService .file-field.input-field .btn {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 250px;
  border: 1px dashed;
  margin-bottom: 2rem;
}
.createService .file-field.input-field .btn > i {
  font-size: 6rem;
}
.createService .file-field.input-field .file-uploader {

}

.createService .input-field.range-selector {
  height: 3rem;
  display: flex;
  align-items: center;
  padding: 0;
}
.createService .input-field.price-tag {
  padding: 0;
}
.createService .input-field.price-tag label, .createService .input-field.range-selector label {
  left: 0;
}
.createService .input-field.price-tag input{
  color: black;
  border-bottom: 1px solid black;
  text-align: right;
}

.createService .chips {
  position: unset;
  margin-top: 0 !important;
  min-height: 3rem !important;
  padding: 0;
}
.createService .chips.focus {
  border-bottom: 1px solid #9e9e9e;
  box-shadow: none;
}
.createService .input {
  position: absolute;
  right: 0;
  top: 0;
  text-align: right;
}
.createService .input:focus {
  border-bottom: 1px solid #26a69a !important;
  box-shadow: 0 1px 0 0 #26a69a !important;
}
.createService .chips .chip.initial i.close {
  display: none;
}
